/**
 * Various flex grid stylings that can be used across the theme.
 * Very good for archive pages. Of course, the foundation grid is
 * also a good choice here, but this is giving a bit more controll
 * and is alowing you to add custom sizings and media queries without
 * distrubting the stylings of foundation.
 */
.flex-grid-wrapper .flex-grid {
  margin: 0 -20px;
  display: flex;
  flex-wrap: wrap; }

.flex-grid-wrapper .grid-item {
  padding: 0 20px; }

.flex-grid-wrapper .col-4 .grid-item {
  flex: 1 1 calc(100% / 4); }

.flex-grid-wrapper .col-3 .grid-item {
  flex: 1 1 calc(100% / 3); }

.flex-grid-wrapper .col-2 .grid-item {
  flex: 1 1 calc(100% / 2); }

@media (max-width: 960px) {
  .flex-grid-wrapper .col-2 .grid-item,
  .flex-grid-wrapper .col-3 .grid-item,
  .flex-grid-wrapper .col-4 .grid-item {
    flex: 1 1 calc(100% / 2); } }

@media (max-width: 640px) {
  .flex-grid-wrapper .col-2 .grid-item,
  .flex-grid-wrapper .col-3 .grid-item,
  .flex-grid-wrapper .col-4 .grid-item {
    flex: 1 1 calc(100% / 1); } }

.flex-grid-wrapper.separators {
  overflow: hidden; }
  .flex-grid-wrapper.separators .grid-item {
    border-right: 1px solid #f0f0f0;
    border-bottom: 1px solid #f0f0f0;
    margin-right: -1px;
    margin-bottom: -2px;
    padding: 40px; }

.inverted .flex-grid-wrapper.separators .grid-item {
  border-right: 1px solid #444;
  border-bottom: 1px solid #444; }

